exports.push([module.id, "\n.hideUpload[data-v-2807dc30] .el-upload{\n    display: none;\n}\n[data-v-2807dc30] .el-form-item .el-input{\n    width: 300px;\n    margin: 0 auto;\n}\n.book-form[data-v-2807dc30]{\n    width: 1000px;\n    margin: 0 auto;\n}\n.form-btn[data-v-2807dc30] .el-button--primary{\n    background-color: #E0B41B;\n    border: 1px solid #E0B41B;\n    margin-left: 60px;\n    width: 120px;\n}\n", "", {"version":3,"sources":["C:/StudyProject/GitPro/vue-springboot-bookstores/ui-bookstore/src/components/user/book/src/components/user/book/addBook.vue"],"names":[],"mappings":";AA+PA;IACA,cAAA;CACA;AACA;IACA,aAAA;IACA,eAAA;CACA;AACA;IACA,cAAA;IACA,eAAA;CACA;AACA;IACA,0BAAA;IACA,0BAAA;IACA,kBAAA;IACA,aAAA;CACA","file":"addBook.vue","sourcesContent":["<template>\n    <div>\n      <el-form :inline=\"true\" :model=\"bookForm\" status-icon  ref=\"bookForm\" label-width=\"80px\" class=\"book-form\">\n        <el-form-item  label=\"ISBN\" class=\"\">\n          <el-input v-model=\"bookForm.iSBN\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"书名\">\n          <el-input v-model=\"bookForm.bookName\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"作者\">\n          <el-input v-model=\"bookForm.author\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"出版社\">\n          <el-input v-model=\"bookForm.publisher\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"出版日期\">\n          <el-date-picker\n            v-model=\"bookForm.publishTime\"\n            type=\"month\"\n            format=\"yyyy-MM\"\n            value-format=\"yyyy-MM-dd\"\n            placeholder=\"选择年月\">\n          </el-date-picker>\n\n        </el-form-item>\n        <el-form-item label=\"数量\">\n<!--          <el-input v-model.number=\"bookForm.storage\"></el-input>-->\n          <el-input-number v-model=\"bookForm.storage\" size=\"large\" controls-position=\"right\"  :min=\"1\" :max=\"999\" style=\"width: auto\">\n          </el-input-number>\n        </el-form-item>\n        <el-form-item label=\"单价\">\n          <el-input v-model=\"bookForm.price\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"折扣\">\n          <el-input v-model=\"bookForm.discount\" placeholder=\"请输入百分比折扣\" @change=\"formatDiscount\">\n            <template slot=\"append\">%</template>\n          </el-input>\n\n        </el-form-item>\n        <el-form-item label=\"现价\">\n          <el-input v-model=\"newprice\" readonly></el-input>\n        </el-form-item>\n       <div style=\"width: 280px;height: 10px\"></div>\n        <el-form-item label=\"书本描述\">\n          <el-input\n            type=\"textarea\"\n            :rows=\"4\"\n            maxlength=\"100\"\n            show-word-limit\n            placeholder=\"几成新呢，描述下你的书吧\"\n            v-model=\"bookForm.tips\"\n            style=\"width: 400px\"></el-input>\n        </el-form-item>\n        <div style=\"width: 280px;height: 10px\"></div>\n        <el-form-item label=\"添加书本图片\" class=\"upload-item\">\n          <el-upload\n            action=\"\"\n            multiple\n            list-type=\"picture-card\"\n            :file-list=\"filelist\"\n            :auto-upload=\"false\"\n            :on-change=\"handleAddChange\"\n            :on-exceed=\"handleExceed\"\n            :on-remove=\"handleRemove\"\n            :limit=\"limitCount\"\n            :class=\"{hideUpload:hideUploadAdd}\"\n          >\n            <i class=\"el-icon-plus\" ></i>\n            <div class=\"el-upload__tip\" slot=\"tip\">（最多上传4张图片）</div>\n            <div slot=\"file\" slot-scope=\"{file}\">\n              <img\n                class=\"el-upload-list__item-thumbnail\"\n                :src=\"file.url\" alt=\"\"\n              >\n              <span class=\"el-upload-list__item-actions\">\n        <span\n          class=\"el-upload-list__item-preview\"\n          @click=\"handlePictureCardPreview(file)\"\n        >\n          <i class=\"el-icon-zoom-in\"></i>\n        </span>\n        <span\n          v-if=\"!disabled\"\n          class=\"el-upload-list__item-delete\"\n          @click=\"handleRemove(file)\"\n        >\n          <i class=\"el-icon-delete\"></i>\n        </span>\n      </span>\n            </div>\n          </el-upload>\n          <!--    放大图-->\n          <el-dialog :visible.sync=\"dialogVisible\">\n            <img width=\"100%\" :src=\"dialogImageUrl\" alt=\"\">\n          </el-dialog>\n        </el-form-item>\n        <div style=\"width: 280px;height: 10px\"></div>\n        <el-form-item class=\"form-btn\">\n          <el-button type=\"primary\" @click=\"resetForm('bookForm')\">取 消</el-button>\n          <el-button type=\"primary\" @click=\"submitForm('bookForm')\">发 布</el-button>\n        </el-form-item>\n      </el-form>\n\n    </div>\n</template>\n\n<script>\n    import {addbook} from \"../../../axios/api\";\n\n    export default {\n      name: \"addBook\",\n      data:function () {\n        return{\n          bookForm:{\n            code:'',\n            userId:'',\n            iSBN:'',\n            bookName:'',\n            author:'',\n            imgurl:'',\n            publisher:'',\n            publishTime:'',\n            price:'',\n            discount:'',\n            upDate:'',\n            storage:'',\n            tips:'',\n            imgList:[],\n          },\n          dialogImageUrl: '',\n          dialogVisible: false,\n          disabled: false,\n          hideUploadAdd:false,\n          filelist:[],\n          limitCount:4\n        }\n      },\n      computed:{\n        newprice:function () {\n          let val=this.bookForm.price*this.bookForm.discount*0.01\n          let price=Number(val).toFixed(3)\n          let realVal = price.substring(0, price.length - 1)\n          return  Number(realVal);\n        }\n      },\n      methods: {\n        formatDiscount(val){\n          const r = /^\\+?[1-9][0-9]*$/;//正整数\n            if(!r.test(val)){\n              this.$message({\n                type:\"warning\",\n                message:\"折扣输入格式有误\",\n                showClose:true\n              })\n          }\n        },\n        newTransformBase64(list, callback, errorCallback) {\n          let promise = [];\n          list.forEach((item, index) => {\n            let p = new Promise((resolve, reject) => {\n              let reader = new FileReader();\n              let obj;\n              if (item.raw) {\n                reader.readAsDataURL(item.raw);\n                reader.onload = function (e) {\n                  obj = reader.result;\n                  resolve(obj);\n                }\n              }\n            })\n            promise.push(p);\n          })\n\n          Promise.all(promise).then(results => {\n            callback(results);\n          }).catch(error => {\n            errorCallback && errorCallback();\n            this.$message.error(\"错误\");\n          });\n        },\n        // 上传change事件\n        handleAddChange(file){\n\n          // 图片大小限制\n          const isLt20M = file.size / 1024 / 1024 < 20;\n          if (!isLt20M) {\n            this.$message.error(\"上传图片大小不能超过 20MB!\");\n            this.filelist.splice(-1, 1);\n          } else {\n            this.filelist.push(file);\n          }\n          this.hideUploadAdd=this.filelist.length>=this.limitCount;\n        },\n\n        // 多选大于限制个数时做提示\n        handleExceed(file) {\n          // 上传文件>=限制个数时隐藏上传组件\n            this.hideUploadAdd=true;\n            this.$message({\n              message: \"上传文件超出限制个数！\",\n              type: \"warning\"\n            })\n\n\n        },\n        handleRemove(file) {\n          //从filelist当前图片uid\n          var list=this.filelist;\n          list.forEach((item,index)=>{\n            if(item.uid==file.uid){\n              console.log(\"uid配对\")\n              this.filelist.splice(index,1)\n            }\n          })\n          this.hideUploadAdd=this.filelist.length>=this.limitCount;\n        },\n        handlePictureCardPreview(file) {\n          this.dialogImageUrl = file.url;\n          this.dialogVisible = true;\n        },\n        submitForm(formname){\n          this.newTransformBase64(this.filelist, (results) => {\n            this.bookForm.imgList=results;\n            this.bookForm.userId=this.$store.state.user.userId;//添加该书本的用户\n            console.log(\"当前用户id\"+this.$store.state.user.userId);\n            this.bookForm.code='10001';//图书类型id\n            addbook(this.bookForm).then(res=>{\n              if(res.data!=0){\n                console.log(res.data)\n                this.$message({\n                  showClose:true,\n                  message:\"发布成功\",\n                  type:'success'\n                })\n                //跳转到详情页\n                this.$router.push({\n                  name:'bookdetail',\n                  params:{\n                    bookId:res.data\n                  }\n                })\n              }\n            })\n          })\n\n\n        },\n        resetForm(formName) {\n          this.$refs[formName].resetFields();\n        },\n      }\n    }\n</script>\n\n<style scoped>\n.hideUpload /deep/ .el-upload{\n    display: none;\n  }\n  /deep/ .el-form-item .el-input{\n    width: 300px;\n    margin: 0 auto;\n  }\n  .book-form{\n    width: 1000px;\n    margin: 0 auto;\n  }\n  .form-btn>>>.el-button--primary{\n    background-color: #E0B41B;\n    border: 1px solid #E0B41B;\n    margin-left: 60px;\n    width: 120px;\n  }\n</style>\n"],"sourceRoot":""}]);

